import { useEffect, useMemo, useState } from 'react';
import { NavLink } from 'react-router-dom';
import { useSpring, animated, config } from 'react-spring';
import { useResponsive } from '../../hooks/useResponsive';
import { menu } from './constants';
import Style from './Style.module.scss';

const Header = () => {
  const [hasMenuOpen, setHasMenuOpen] = useState(false);

  const [menuTransitionStatus, setMenuTransitionStatus] = useState(false);

  const [immediate, setImmediate] = useState(true);

  const { isMobile } = useResponsive();

  const menuSpring = useSpring({
    from: {
      transform: 'translateY(-30px)',
      rate: 0,
    },
    to: {
      transform: 'translateY(0px)',
      rate: 100,
    },
    config: {
      mass: 1,
      duration: 200,
    },
    onChange(ds) {
      if (ds.value.rate >= 60 && !menuTransitionStatus) {
        setHasMenuOpen(false);
      }
    },
    reset: false,
    reverse: menuTransitionStatus,
  });

  useEffect(() => {
    setImmediate(false);
  }, []);

  const width = useMemo(() => (isMobile ? 70 : 50), [isMobile]);

  const spring = useSpring({
    from: {
      width: `${width}vw`,
      transform: 'translateX(0vw)',
      background: 'rgba(0, 45, 113, 1)',
      rate: 100,
    },
    to: {
      width: `${width}vw`,
      transform: `translateX(-${width}vw)`,
      background: 'rgba(0, 45, 113, 0.9)',
      rate: 0,
    },
    immediate,
    reset: false,
    reverse: hasMenuOpen,
    config: {
      clamp: false,
      easing: config.slow,
    },
    onChange(ds) {
      if (ds.value.rate >= 60) {
        setMenuTransitionStatus(hasMenuOpen);
      }
    },
    // onRest(ds) {},
  });

  const maskSpring = useSpring({
    from: {
      background: 'rgba(0, 0, 0, 0.9)',
      rate: 100,
    },
    to: {
      background: 'rgba(0, 0, 0, 0.8)',
      rate: 0,
    },
    immediate,
    reset: false,
    reverse: menuTransitionStatus,
    config: {
      mass: 1,
      clamp: false,
      easing: config.slow,
    },
  });

  const showMenu = () => {
    setHasMenuOpen(!hasMenuOpen);
  };

  const hideMenu = () => {
    setMenuTransitionStatus(false);
  };

  const handleMenuButtonClick = () => {
    if (hasMenuOpen) {
      hideMenu();
    } else {
      showMenu();
    }
  };

  return (
    <>
      <div className={Style.Header}>
        <div className={Style.HeaderLogo}>
          <NavLink to="/">
            <img className={Style.HeaderLogoImage} src="/static/icons/logo.svg" alt="eleatek" />
            <img className={Style.HeaderLogoText} src="/static/icons/logoText.svg" alt="eleatek" />
          </NavLink>
        </div>
        <div className={Style.HeaderMenu}>
          <div className={Style.HeaderMenuItem} onClick={() => handleMenuButtonClick()}>
            MENU
          </div>
          <div className={Style.HeaderMenuItem}>STATISTICS</div>
        </div>
      </div>

      <animated.div style={spring} className={Style.MenuWarp}>
        <div className={Style.MenuLabel}>MENU</div>
        <animated.div style={menuSpring}>
          {menu.map((menuItem) => (
            <div
              className={Style.MenuItem}
              key={menuItem.path}
              onClick={() => handleMenuButtonClick()}>
              <NavLink to={menuItem.path}>{menuItem.name}</NavLink>

              {menuItem.children?.map((childMenuItem) => (
                <div
                  className={Style.ChildMenuItem}
                  key={childMenuItem.path}
                  onClick={() => handleMenuButtonClick()}>
                  <NavLink to={childMenuItem.path}>{childMenuItem.name}</NavLink>
                </div>
              ))}
            </div>
          ))}
        </animated.div>
      </animated.div>

      {hasMenuOpen && (
        <animated.div
          style={maskSpring}
          className={Style.MenuMask}
          onClick={() => handleMenuButtonClick()}
        />
      )}
    </>
  );
};

export default Header;
