/* eslint-disable react/no-array-index-key */
import { useSpring, animated, easings, useSprings } from 'react-spring';
import { useMemo, useState, useEffect } from 'react';
import range from 'lodash/range';
import throttle from 'lodash/throttle';
import { useHistory } from 'react-router-dom';
import { usePrevious } from '../../hooks/usePrevious';
import { useResponsive } from '../../hooks/useResponsive';
import Style from './Solution.module.scss';

const SolutionTransition = ({ left, right, total, rightExtend }) => {
  const contentLength = Object.keys(left).length;

  const [transitionIndex, setTransitionIndex] = useState(0);

  const [contentIndex, setContentIndex] = useState(0);

  const oldTransitionIndex = usePrevious(transitionIndex);

  const { isMobile } = useResponsive();

  const actionIsNext = useMemo(
    () => oldTransitionIndex < transitionIndex,
    [oldTransitionIndex, transitionIndex],
  );

  const isPlural = useMemo(() => transitionIndex % 2 === 0, [transitionIndex]);

  const contentIndexIsPlural = useMemo(() => contentIndex % 2 === 0, [contentIndex]);

  const [immediate, setImmediate] = useState(true);

  const [maskTransitionStatus, setMaskTransitionStatus] = useState(false);

  const handelsetMaskTransitionStatus = throttle((status) => {
    setMaskTransitionStatus(status);
  }, 300);

  const history = useHistory();

  useEffect(() => {
    setImmediate(false);
  }, []);

  const handleChangeIndex = (v) => {
    if (v === -1) {
      history.push('/');
      return;
    }

    if (v === total) return;

    setTransitionIndex(v);
  };

  // eslint-disable-next-line arrow-body-style
  const getMaskSpringConfig = ({ status }) => {
    // const easing = actionIsNext ? easings.easeInSine : easings.easeOutSine;

    return {
      from: {
        transform: isMobile
          ? `translateX(${status ? 100 : -100}vw)`
          : `translateY(${status ? 100 : -100}vh)`,
        height: isMobile ? '50vh' : '100vh',
        rate: 100,
      },
      to: {
        transform: isMobile
          ? `translateX(${status ? -100 : 100}vw)`
          : `translateY(${status ? -100 : 100}vh)`,
        rate: 0,
      },
      immediate,
      reset: false,
      reverse: maskTransitionStatus,
      config: {
        mass: 1,
        clamp: false,
        // easing,
        duration: 500,
      },
      onChange(ds) {
        if ((!isPlural && ds.value.rate >= 30) || (isPlural && ds.value.rate <= 30)) {
          setContentIndex(transitionIndex);
        }
      },
    };
  };

  const getContentSpringConfig = ({ status, index }) => {
    const isCurrent = index === transitionIndex;

    const contentIsCurrent = index === contentIndex;

    const easing =
      contentIsCurrent < transitionIndex ? easings.easeInBounce : easings.easeOutBounce;

    return {
      from: {
        transform: isMobile ? 'translateX(0px)' : 'translateY(0px)',
        rate: 100,
        opacity: 1,
        zIndex: 9 - index,
      },
      to: {
        transform: isMobile
          ? `translateX(${status ? 50 : -50}px)`
          : `translateY(${status ? 50 : -50}px)`,
        opacity: 0,
        zIndex: 9 - index,
        rate: 0,
      },
      config: {
        mass: 1,
        easing,
      },
      delay: immediate || !isCurrent ? 0 : 600,
      reset: false,
      immediate: index !== 0 && immediate,
      reverse: isCurrent,
      onChange(ds) {
        if (
          contentIsCurrent &&
          ((!isPlural && ds.value.rate >= 50) || (isPlural && ds.value.rate <= 50))
        ) {
          handelsetMaskTransitionStatus(!isPlural);
        }
      },
    };
  };

  const leftMaskSpring = useSpring(getMaskSpringConfig({ status: true }));

  const rightMaskSpring = useSpring(getMaskSpringConfig({ status: false }));

  const leftContentSprings = useSprings(
    contentLength,
    range(0, contentLength).map((item, index) => getContentSpringConfig({ status: true, index })),
  );

  const rightContentSprings = useSprings(
    contentLength,
    range(0, contentLength).map((item, index) => getContentSpringConfig({ status: false, index })),
  );

  const rightBgSpring = useSpring({
    from: {
      background: 'rgba(0, 45, 113, 0)',
    },
    to: {
      background: 'rgba(0, 45, 113, 1)',
    },
    reverse: contentIndexIsPlural,
    config: {
      mass: 1,
      easing: actionIsNext ? easings.easeInBounce : easings.easeOutBounce,
    },
    reset: false,
    immediate,
  });

  const leftBgSpring = useSpring({
    from: {
      background: 'rgba(0, 45, 113, 1)',
    },
    to: {
      background: 'rgba(0, 45, 113, 0)',
    },
    reverse: contentIndexIsPlural,
    config: {
      mass: 1,
      easing: actionIsNext ? easings.easeInBounce : easings.easeOutBounce,
    },
    reset: false,
    immediate,
  });

  return (
    <animated.div className={Style.TransitionWarp}>
      <animated.div style={leftBgSpring} className={Style.Left}>
        <animated.div style={leftMaskSpring} className={Style.LeftMask} />

        {leftContentSprings.map((springConf, i) => (
          <div
            className={Style.ContentWarp}
            style={{ zIndex: i === transitionIndex ? 9 : 9 - i }}
            key={`m-${i}`}>
            <animated.div style={springConf} key={i} className={Style.LeftContent}>
              {left[i]}
              {contentIndex !== total - 1 && (
                <div
                  className={Style.NextBtn}
                  onClick={() => handleChangeIndex(transitionIndex + 1)}>
                  <img src="/static/solution/nextArrow.svg" alt="foundUs" />
                </div>
              )}
            </animated.div>
          </div>
        ))}
      </animated.div>

      <animated.div style={rightBgSpring} className={Style.Right}>
        <animated.div style={rightMaskSpring} className={Style.RightMask} />

        {rightContentSprings.map((springConf, i) => (
          <div
            className={Style.ContentWarp}
            style={{ zIndex: i === transitionIndex ? 9 : 9 - i }}
            key={`m-${i}`}>
            <animated.div style={springConf} key={i} className={Style.RightContent}>
              <div className={Style.BackBtn} onClick={() => handleChangeIndex(transitionIndex - 1)}>
                <img src="/static/solution/prevArrow.svg" alt="foundUs" /> back
              </div>

              {right[i]}
            </animated.div>
          </div>
        ))}

        {rightExtend?.(contentIndex)}
      </animated.div>

      <div className={Style.Progress}>
        <div
          style={{
            width: `${100 * ((transitionIndex + 1) / total)}%`,
          }}
          className={Style.ProgressColor}
        />
      </div>
    </animated.div>
  );
};

export default SolutionTransition;
